<template>

        <vuestic-widget v-if="ok" class="chart-widget" v-bind:headerText="title">
          <vuestic-chart v-bind:data="lineChartData" type="horizontal-bar"></vuestic-chart>
        </vuestic-widget>

</template>

<script>
import VuesticChart from '../vuestic-components/vuestic-chart/VuesticChart.vue'
// import LineChartData from './LineChartData'
import utils from 'services/utils'
import store from 'vuex-store'
let palette = store.getters.palette

export default {
  props:['url','title'],
  name: 'horichart',
  components: {
    VuesticChart
  },
  created () {
    this.getdata()
  },
  methods: {
    getdata: function () {
      this.$http.get(this.url).then(
        function (res) {
          // 处理成功的结果
          this.json = res.data
          this.$set(this.lineChartData, 'labels', this.json.labels)
          for (var i = 0; i < this.json.datasets.length; i++) {
            this.lineChartData.datasets.push({
              label: '这里自己随便弄咯，加几个都行',
              backgroundColor: '',
              borderColor: palette.transparent,
              data: []
            })
            this.$set(this.lineChartData.datasets[i], 'backgroundColor', utils.hex2rgb(this.json.datasets[i].backgroundColor, 0.6).css)
            this.$set(this.lineChartData.datasets[i], 'data', this.json.datasets[i].data)
            this.$set(this.lineChartData.datasets[i], 'label', this.json.datasets[i].label)
          }
          this.ok = true

        }, function (res) {
          console.log('fail json')
        }
      )
    }
  },
  data: function () {
    return {
      url:'',
      title:'',
      ok: false,
      json: null,
      lineChartData: {
        labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
        datasets: []
      }

    }
  }
}
</script>

<style lang="scss">
.ui-typography {
  .typo-articles {
    margin-bottom: 6.25rem;
    width: 90%;
  }

  .widget-body {
    padding: 3.75rem 4.7rem 0 2.2rem !important;
  }

  .col-md-6 {
    padding-right: 0;
  }

  .vue-lists ul,
  ol {
    width: 85%;
  }
  .widget.chart-widget {
    .widget-body {
      height: 550px;
    }
  }
}
</style>



